<template>
  <div id="app" @mousedown="show = !show">
    <transition name="star-fade">
        <img v-if="show" src="/static/star4.jpg" class="star"/>

    </transition>

  </div>
</template>

<script>
export default {
  name: 'App',
  data: function () {
    return {

      show: true
    }
  },
  methods: {

  },
  watch: {
    show: {
      handler: function () {
        clearTimeout(this.timer)

        this.timer = setTimeout(() => {
          this.show = true
        }, 5000)
      }
    }
  },
  mounted () {
    const audio = new Audio('/static/fallforyou_all.mp3')
    audio.play()
  }
}
</script>

<style>
/*#app {*/
/*  font-family: 'Avenir', Helvetica, Arial, sans-serif;*/
/*  -webkit-font-smoothing: antialiased;*/
/*  -moz-osx-font-smoothing: grayscale;*/
/*  text-align: center;*/
/*  color: #2c3e50;*/
/*  margin-top: 60px;*/
/*}*/
body {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  font-family: 'Montserrat', sans-serif;
  background: url("/static/background_d.jpg");
  background-size:100% 100%;
  cursor: url('/static/star.jpg'), default;
  -webkit-overflow-scrolling: touch;
}

#app {
  -webkit-tap-highlight-color: transparent;
}

svg {
  width: 50vw;
  height: 50vh;
  max-height: 400px;
  margin-left: 20vw;
}

p {
  font-size: 16px;
  color: white;
  text-align: center;
}
star{
  width: fit-content;
  height: fit-content;
  margin-left: 0;
  margin-top: 100px;

}

.star-fade-enter-active{
}
.star-fade-leave-active{
  transition: all 1.8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
}
.star-fade-leave-to{
  transform: translateX(1000px);
  opacity: 0;
}

@media screen and (max-width: 600px) {
  svg {
    max-height: 200px !important;
    margin-left: 0 !important;
  }
}
</style>



